.App {
  text-align: center;
  display: flex;
  justify-content: space-around;
}
.Kitty{
  width: 200px;
  height: 200px;
  background: linear-gradient(skyblue, blue, black);
border: 1px solid black;
box-shadow: 5px 5px 5px 5px;
margin-bottom: 50px;
border-radius: 10px;
margin-top: 20px;


}
.text{
  width: 300px;
  height: 300px;
  background: linear-gradient(skyblue, blue, black);
  border: 1px solid black;
  box-shadow: 5px 5px 5px 4px;
  padding-top: 20px;
  font-weight: 400px;
  font-size: 35px;
  
}


img{
  width: 100px;
  height: 100px;
  border-radius: 50%;
  padding-top: 20px;
}


.name{
  color: yellow;
  font-size: 25px;
}

.anjoy{
  color: yellow;
  font-weight: bold;
  font-size: 40px;
  
}

.date{
  color: yellow;
}